.container {
    display: flex;
    height: 100%;
}
  
.left {
    flex: 3;
    display: flex;
    align-items: center;
    overflow-y: auto;
    height: 100%;
    flex-direction: column;
}

.right {
    flex: 4;
    overflow-y: scroll;
    height: 100vh;
    justify-content: center;
    text-align: center;
}

.quote {
    flex: 2;
    display: flex;
    align-items: center;
    align-self: center;
    justify-content: center;
    height: 100%;
    flex-direction: column;
    overflow-y: hidden;
    padding-bottom: 14vh;
}

.info-icon {
    width: 15px;
    align-items: center;
}

.privacy {
    align-items: center;
    display: flex;
    justify-content: center;
}

.privacy-select {
    height: 100%;
}

.page-padding {
    padding-top: 24px;
}

.thanks-container {
    padding-top: 2vh;
    margin-bottom: 100px;
}

.thanks-form {
    margin-top: 16px;
    margin-bottom: 8px;
    width: 90%;
}

.absolute-center {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 200px;
    height: 200px;
    transform: translate(-50%, -50%);
    z-index: 1000;
    pointer-events: none;
}

.center-loader {
    top: 20%;
    left: 80%;
    margin: 8px;
    margin-bottom: 40px;
}

.text-profile {
    margin: 16px;
    font-size: 12px;
    font-weight: bold;
    color: #50C878;
}